'use client'

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react'

export interface Supplier {
  id: string
  name: string
  cnpj: string
  phone: string
  city: string
  state: string
  status: 'active' | 'inactive'
  createdAt: string
}

export interface Institution {
  id: string
  name: string
  type: 'school' | 'center' | 'other'
  cnpj: string
  email: string
  phone: string
  address: string
  city: string
  principalName: string
  status: 'active' | 'inactive'
  createdAt: string
}

export interface Material {
  id: string
  name: string
  category: string
  description: string
  unit: string
  quantity: number
  minQuantity: number
  supplier: string
  unitPrice: number
  lastUpdate: string
}

export interface Request {
  id: string
  requestNumber: string
  institution: string
  requiredDate: string
  status: 'pending' | 'approved' | 'delivered' | 'cancelled'
  items: RequestItem[]
  totalValue: number
  createdAt: string
}

export interface RequestItem {
  materialId: string
  materialName: string
  quantity: number
  unitPrice: number
  total: number
}

export interface Output {
  id: string
  materialId: string
  materialName: string
  quantity: number
  unit: string
  institutionId?: string
  institutionName?: string
  reason: string
  responsible: string
  outputDate: string
  createdAt: string
}

interface DataContextType {
  suppliers: Supplier[]
  institutions: Institution[]
  materials: Material[]
  requests: Request[]
  outputs: Output[]
  customCategories: string[]
  addSupplier: (supplier: Omit<Supplier, 'id' | 'createdAt'>) => void
  updateSupplier: (id: string, supplier: Partial<Supplier>) => void
  deleteSupplier: (id: string) => void
  addInstitution: (institution: Omit<Institution, 'id' | 'createdAt'>) => void
  updateInstitution: (id: string, institution: Partial<Institution>) => void
  deleteInstitution: (id: string) => void
  addMaterial: (material: Omit<Material, 'id' | 'lastUpdate'>) => void
  updateMaterial: (id: string, material: Partial<Material>) => void
  deleteMaterial: (id: string) => void
  addRequest: (request: Omit<Request, 'id' | 'requestNumber' | 'createdAt'>) => void
  addRequests: (requests: Array<Omit<Request, 'id' | 'requestNumber' | 'createdAt'>>) => void
  updateRequest: (id: string, request: Partial<Request>) => void
  deleteRequest: (id: string) => void
  addOutput: (output: Omit<Output, 'id' | 'createdAt'>) => void
  updateOutput: (id: string, output: Partial<Output>) => void
  deleteOutput: (id: string) => void
  addCustomCategory: (category: string) => void
  seedInitialData: () => void
}

